'use client';

import { useState } from 'react';
import { useAccount } from 'wagmi';
import { parseEther } from 'viem';
import { useContract } from '@/lib/contract';
import toast from 'react-hot-toast';

interface BattleArenaProps {
  samuraiId: number;
  samuraiStats: {
    strength: number;
    agility: number;
    intelligence: number;
    experience: number;
  };
  onBattleComplete: () => void;
}

export default function BattleArena({
  samuraiId,
  samuraiStats,
  onBattleComplete,
}: BattleArenaProps) {
  const { address } = useAccount();
  const [betAmount, setBetAmount] = useState('0.01');
  const [isBattling, setIsBattling] = useState(false);

  const { startBattle, isPending, isSuccess } = useContract();

  const handleBattle = async () => {
    if (!address) {
      toast.error('Please connect your wallet first');
      return;
    }

    if (!betAmount || parseFloat(betAmount) <= 0) {
      toast.error('Please enter a valid bet amount');
      return;
    }

    setIsBattling(true);
    toast.loading('Initiating battle...');

    try {
      // For now, we'll use a placeholder opponent address
      // In a real implementation, you'd select an opponent
      const opponentAddress =
        '0x0000000000000000000000000000000000000001' as `0x${string}`;
      await startBattle(opponentAddress);
    } catch (error) {
      console.error('Battle error:', error);
      toast.error('Failed to initiate battle');
      setIsBattling(false);
    }
  };

  // Handle transaction success
  if (isSuccess && isBattling) {
    setIsBattling(false);
    toast.success('Battle completed! Check your rewards.');
    onBattleComplete();
  }

  return (
    <div className="bg-white/10 backdrop-blur-md rounded-lg p-6 mb-6">
      <h2 className="text-2xl font-bold text-white mb-4">‚öîÔ∏è Battle Arena</h2>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <div className="space-y-4">
          <h3 className="text-xl font-semibold text-white">Your Samurai</h3>
          <div className="bg-gray-800/50 rounded-lg p-4 space-y-2">
            <p className="text-white">Strength: {samuraiStats.strength}</p>
            <p className="text-white">Agility: {samuraiStats.agility}</p>
            <p className="text-white">
              Intelligence: {samuraiStats.intelligence}
            </p>
            <p className="text-white">Experience: {samuraiStats.experience}</p>
          </div>
        </div>

        <div className="space-y-4">
          <h3 className="text-xl font-semibold text-white">Battle Settings</h3>
          <div className="space-y-4">
            <div>
              <label className="block text-white text-sm font-medium mb-2">
                Bet Amount (RON)
              </label>
              <input
                type="number"
                value={betAmount}
                onChange={(e) => setBetAmount(e.target.value)}
                min="0.01"
                step="0.01"
                className="w-full px-3 py-2 bg-gray-800 text-white rounded border border-gray-600 focus:border-blue-500 focus:outline-none"
                placeholder="0.01"
              />
            </div>

            <button
              onClick={handleBattle}
              disabled={isBattling || isPending || !address}
              className="w-full px-6 py-3 bg-red-600 text-white rounded-lg hover:bg-red-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
            >
              {isBattling || isPending ? 'Battling...' : '‚öîÔ∏è Enter Battle'}
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
