'use client';

import { useState } from 'react';
import { useAccount } from 'wagmi';
import { useContract } from '@/lib/contract';
import { Samurai } from '@/types/game';
import toast from 'react-hot-toast';

interface SamuraiCardProps {
  samurai: Samurai | null;
  onSamuraiUpdate: () => void;
}

export default function SamuraiCard({
  samurai,
  onSamuraiUpdate,
}: SamuraiCardProps) {
  const { address } = useAccount();
  const { createSamurai, upgradeStat, isPending, isSuccess } = useContract();
  const [samuraiName, setSamuraiName] = useState('');
  const [isCreating, setIsCreating] = useState(false);

  const handleCreateSamurai = async () => {
    if (!address) {
      toast.error('Please connect your wallet first');
      return;
    }

    if (!samuraiName.trim()) {
      toast.error('Please enter a samurai name');
      return;
    }

    setIsCreating(true);
    toast.loading('Creating your samurai...');

    try {
      await createSamurai(samuraiName);
      setSamuraiName('');
    } catch (error) {
      console.error('Create samurai error:', error);
      toast.error('Failed to create samurai');
      setIsCreating(false);
    }
  };

  const handleUpgradeStat = async (statIndex: number) => {
    if (!address) {
      toast.error('Please connect your wallet first');
      return;
    }

    if (!samurai) {
      toast.error('No samurai found');
      return;
    }

    toast.loading('Upgrading stat...');

    try {
      await upgradeStat(statIndex);
    } catch (error) {
      console.error('Upgrade stat error:', error);
      toast.error('Failed to upgrade stat');
    }
  };

  // Handle transaction success
  if (isSuccess) {
    if (isCreating) {
      setIsCreating(false);
      toast.success('Samurai created successfully!');
      onSamuraiUpdate();
    } else {
      toast.success('Stat upgraded successfully!');
      onSamuraiUpdate();
    }
  }

  if (!samurai) {
    return (
      <div className="bg-white/10 backdrop-blur-md rounded-lg p-6 mb-6">
        <h2 className="text-2xl font-bold text-white mb-4">
          üó°Ô∏è Create Your Samurai
        </h2>

        <div className="space-y-4">
          <div>
            <label className="block text-white text-sm font-medium mb-2">
              Samurai Name
            </label>
            <input
              type="text"
              value={samuraiName}
              onChange={(e) => setSamuraiName(e.target.value)}
              className="w-full px-3 py-2 bg-gray-800 text-white rounded border border-gray-600 focus:border-blue-500 focus:outline-none"
              placeholder="Enter your samurai's name"
              maxLength={32}
            />
          </div>

          <button
            onClick={handleCreateSamurai}
            disabled={isCreating || isPending || !samuraiName.trim()}
            className="w-full px-6 py-3 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
          >
            {isCreating || isPending ? 'Creating...' : 'üó°Ô∏è Create Samurai'}
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="bg-white/10 backdrop-blur-md rounded-lg p-6 mb-6">
      <h2 className="text-2xl font-bold text-white mb-4">üó°Ô∏è Your Samurai</h2>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <div className="space-y-4">
          <h3 className="text-xl font-semibold text-white">{samurai.name}</h3>
          <div className="bg-gray-800/50 rounded-lg p-4 space-y-2">
            <p className="text-white">
              Strength: {samurai.strength.toString()}
            </p>
            <p className="text-white">Defense: {samurai.defense.toString()}</p>
            <p className="text-white">Speed: {samurai.speed.toString()}</p>
            <p className="text-white">Health: {samurai.health.toString()}</p>
            <p className="text-white">
              Experience: {samurai.experience.toString()}
            </p>
            <p className="text-white">Level: {samurai.level.toString()}</p>
          </div>
        </div>

        <div className="space-y-4">
          <h3 className="text-xl font-semibold text-white">Upgrade Stats</h3>
          <div className="space-y-3">
            <button
              onClick={() => handleUpgradeStat(0)}
              disabled={isPending}
              className="w-full px-4 py-2 bg-red-600 text-white rounded hover:bg-red-700 disabled:opacity-50 transition-colors"
            >
              Upgrade Strength (Cost: 1 EXP)
            </button>

            <button
              onClick={() => handleUpgradeStat(1)}
              disabled={isPending}
              className="w-full px-4 py-2 bg-green-600 text-white rounded hover:bg-green-700 disabled:opacity-50 transition-colors"
            >
              Upgrade Defense (Cost: 1 EXP)
            </button>

            <button
              onClick={() => handleUpgradeStat(2)}
              disabled={isPending}
              className="w-full px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700 disabled:opacity-50 transition-colors"
            >
              Upgrade Speed (Cost: 1 EXP)
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
